@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #1f1f38;
  --color-bg-variant: #2c2c6c;
  --color-primary: #4db5ff;
  --color-primar: #4db5ff;
  --color-primary-varient: rgba(77, 181, 255, 0.4);
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Poppins', sans-serif;
  background: #1f1f38;
  background: var(--color-bg);
  color: #fff;
  color: var(--color-white);
  line-height: 1.7;
 /* background-image: url(https://img.freepik.com/free-photo/blue-paperboard-texture_1409-1329.jpg?t=st=1648816292~exp=1648816892~hmac=90963e4fc7d2e72480411a84f53f62a25630138406e05a350970eec8c867c7b2&);*/
  background-image: url(data:image/png;base64,);
}

.container {
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section > h2,
section > h5 {
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-light);
}

section > h2 {
  color: #4db5ff;
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-light);
}

a {
  color: #4db5ff;
  color: var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

a:hover {
  color: #fff;
  color: var(--color-white);
}

.btn {
  display: inline-block;
  font-weight: 500;
  line-height: 1.5;
  color: #4f4f4f;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
  background-color: transparent;
  border: 0.125rem solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 0.75rem;
  border-radius: 1.25rem;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.btn:hover {
  background: #fff;
  background: var(--color-white);
  color: #1f1f38;
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
    color: #fff;
    background-color: #1266f1;
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* media querry  (medium)*/

@media screen and (max-width: 1024px) {
  .container {
    width: 86%;
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

/*==== media querry  (small devives) ==== */

@media screen and (max-width: 600px) {
  .container {
    width: 90%;
    width: var(--container-width-sm);
  }

  section {
    margin-top: 2rem;
  }
}

.about_container {
  display: grid;
  grid-template-columns: 35% 50%;
  grid-gap: 15%;
  gap: 15%;
}

.about_me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    60deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}


/* for just one hover tranistion
.about_me-image {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition); 
}
.about_me-image:hover {
  transform: rotate(0);
 }
for just one hover tranistion */

/*for infinte animation*/
.about_me-image {
  border-radius: 2rem;
  overflow: hidden;
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg);
  -webkit-animation: rotate 1s ease infinite alternate;
          animation: rotate 1s ease infinite alternate;
}

@-webkit-keyframes rotate {
  100% {
    -webkit-transform: translate(0%);
            transform: translate(0%);
  }
}

@keyframes rotate {
  100% {
    -webkit-transform: translate(0%);
            transform: translate(0%);
  }
}

/*for infinte animation*/

.about_cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.about_card {
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}

.about_card:hover {
  background: transparent;
  border-color: var(--color-primary-varient);
  cursor: default;
}

.about_icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.about_card h5 {
  font-size: 0.95rem;
}

.about_card small {
  font-size: 0.7rem;
  color: var(--color-light);
}

.about_content p {
  margin: 2rem 0 2.6rem;
  color: var(--color-light);
}

/* media querry  (medium)*/

@media screen and (max-width: 1024px) {
  .about_container {
    grid-template-columns: 1fr;
    grid-gap: 0;
    gap: 0;
  }

  .about_me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about_content p {
    margin: 1rem 0 1.5rem;
  }
}

/*==== media querry  (small devives) ==== */

@media screen and (max-width: 600px) {
  .about_me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about_cards {
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    gap: 1rem;
  }
  .about_content {
    text-align: center;
  }
  .about_content p {
    margin: 1.5rem 0;
  }
}

header {
  height: 100vh;
  padding-top: 6rem;
  overflow: hidden;
}
.btnn {
width: -webkit-max-content;
width: max-content;
display: inline-block;
color: var(--color-primary);
padding: 0.75rem 1.2rem;
border-radius: 0.4rem;
cursor: pointer;
border: 1px solid var(--color-primary);
transition: var(--transition);
}

.btnn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

.header_container {
  text-align: center;
  height: 100%;
  position: relative;
}

.cta {
  margin-top: 2.5rem;
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  justify-content: center;
}



/*Header social*/

.header_socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header_socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/*   Me   */

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 17rem;
  height: 24rem;
  position: absolute;
  left: calc(50% - 7rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 3rem 0.5rem 1.5rem 1.5rem;
}

/* Scroll Down*/


.scroll-down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}


/* media querry  (medium)*/

@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}


/*==== media querry  (small devives) ==== */

@media screen and (max-width: 600px) {
  header {
    height: 100vh;
    padding-top: 4rem;
  }

  .me {
    position: absolute;
    left: calc(50% - 8rem);
  }


  .header_socials,
  .scroll-down {
    display: none;

  }
}

.cont {
  margin-top: 5rem;
}
.exp_cont {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem;
  gap: 2rem;
  transition: var(--transition);
}

.exp_cont > div {
  background: var(--color-bg-variant);
  padding: 2.4rem 5rem;
  border-radius: 2rem;
  border: 1px solid transparent;
}

.exp_cont > div:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.exp_cont > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--color-primary);
}

.exp_content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.exp_details {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}


.exp_details-icons {
  margin-top: 6px;
  color: var(--color-primary);
}


/* media querry  (medium)*/

@media screen and (max-width: 1024px) {
  .exp_cont {
    grid-template-columns: 1fr;
  }
  .exp_cont > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
  }

  .exp_content {
    padding: 1rem;
  }
}

/*==== media querry  (small devives) ==== */

@media screen and (max-width: 600px) {
 .exp_cont {
   grid-gap: 1rem;
   gap: 1rem;
 }

 .exp_cont > div {
   width: 100%;
   padding: 2rem 1rem;
 }
}
nav {
  background: rgba(0, 0, 0, 0.3);
  width: -webkit-max-content;
  width: max-content;
  display: block;
  padding: 0.1rem 0.3rem;
  z-index: 2;
  position: fixed;
  left: 52%;
  -webkit-transform: translateX(-52%);
          transform: translateX(-52%);
  bottom: 1rem;
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  border-radius: 3rem;
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
}

nav a {
   background: transparent;
   padding: 0.9rem;
   border-radius: 50%;
   display: flex;
   color: var(--color-light);
   font-size: 1.1rem;
}

nav a:hover {
  background: rgba(0, 0, 0, 0.3);
}

nav a:active {
  background: var(--color-bg);
  color: var(--color-white);
}

