.port_cont {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2.5rem;
  gap: 2.5rem;
}

.port_item {
  background: var(--color-bg-variant);
  padding: 1.3rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.port_item:hover {
  border-color: var(--color-primary-varient);
  background: transparent;
}

.port_item-image {
  border-radius: 1.5rem;
  overflow: hidden;
}

.port_item h3 {
  margin: 1.2rem 0 2rem;
}

.port_item-cta {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
}


/* media querry  (medium)*/

