    grid-template-columns: 1fr 1fr;
    grid-gap: 1.2rem;
    gap: 1.2rem;
  }
}

/*==== media querry  (small devives) ==== */

@media screen and (max-width: 600px) {
  .port_cont {
    grid-template-columns: 1fr;
