header {
  height: 100vh;
  padding-top: 6rem;
  overflow: hidden;
}
.btnn {
width: max-content;
display: inline-block;
color: var(--color-primary);
padding: 0.75rem 1.2rem;
border-radius: 0.4rem;
cursor: pointer;
border: 1px solid var(--color-primary);
transition: var(--transition);
}

.btnn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

.header_container {
  text-align: center;
  height: 100%;
  position: relative;
}

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}



/*Header social*/

.header_socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header_socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/*   Me   */

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 17rem;
  height: 24rem;
  position: absolute;
  left: calc(50% - 7rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 3rem 0.5rem 1.5rem 1.5rem;
}

/* Scroll Down*/


.scroll-down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}


/* media querry  (medium)*/

@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}


/*==== media querry  (small devives) ==== */

@media screen and (max-width: 600px) {
  header {
    height: 100vh;
    padding-top: 4rem;
  }

  .me {
    position: absolute;
    left: calc(50% - 8rem);
  }


  .header_socials,
  .scroll-down {
    display: none;

  }
}